@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,300;0,400;0,600;0,700;0,800;0,900;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Nunito', sans-serif;
  overflow-x: hidden;
}

header {
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.div-container-header {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

header input[type="text"] {
    border: none;
    border-bottom: 1px solid;
    outline: none;
    width: 500px;
}

.content-div-header {
    display: flex;
    align-items: center;
}
.content-div-header p {
    margin: 0 15px 0 0;
}

.info-cart{
    background-color: red;
    color: white;
    border-radius: 50%;
    height: 20px;
    width: 20px;
    text-align: center;
}


.products {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 10px 0 0;
    height: 400px;
}

.size-svg{
    font-size: 50px;
    cursor: pointer;
}

.card-product {
    width: 200px;
    height: 350px;
    margin: 0 10px 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.product-name{
    font-size: 12px;
    white-space: nowrap;
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
}
.product-price{
    font-size: 16px;
    font-weight: bolder;
}

.btn-card-product {
    padding: 15px;
    background-color: #000000;
    color: #E5E5E5;
    border-radius: 5px;
    cursor: pointer;
    border: 2px solid #000;
    outline: none;
    width: 100%;
    max-height: 45px;
}

.btn-card-product:hover {
    display: block;
}

form {
    display: flex;
    justify-content: center;
    margin: 15px;
    text-align: center;
    height: 80px;
}

form input[type="text"] {
    padding: 15px;
    margin: 0 10px 0 0;
}
form button {
    padding: 15px;
    background-color: #000000;
    color: #E5E5E5;
    border-radius: 5px;
    cursor: pointer;
    border: 2px solid #000;
    outline: none;
    width: 130px;
    max-height: 49px;
}
footer {
    background-color: black;
    color: #E5E5E5;
    height: 150px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.div-trace {
    width: 30px;
    height: 3px;
    background-color: white;
}

.content-div-footer {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.content-div-footer button {
    margin: 5px;
    height: 40px;
    width: 180px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
}

.img-footer {
    background-image: url(/static/media/corebiz-450x360.f8c1d0ba.jpg);
    width: 100%;
    max-width: 190px;
    height: 100%;
    background-position: center;
    background-size: cover;
}

.error{
    color: red;
    font-size: 16px;
    font-weight: 700;
}

.loading {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loader{
    border: 16px solid #c7c7c7;
    border-radius: 50%;
    border-top: 16px solid #000000;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 2s linear infinite;
}
@-webkit-keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
.carousel {
    width: 100%;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.slider {
    display: flex;
    justify-content: center;
    align-items: center;
}
.carousel svg {
    font-size: 36px;
    z-index: 10;
}

.image {
    width: 100%;
    max-width: 1920px;
    height: 400px;
}

.right-arrow {
    position: absolute;
    top: 35%;
    right: 32px;
    font-size: 3rem;
    color: #000;
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.left-arrow {
    position: absolute;
    top: 35%;
    left: 32px;
    font-size: 3rem;
    color: #000;
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.slide {
    opacity: 0;
    transition-duration: 1s ease;
}

.slide.active {
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.08);
}
